/* Global styles */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

:root {
  /* Enhanced color scheme for better visibility */
  --primary: #3b82f6;
  --primary-light: #60a5fa;
  --primary-dark: #2563eb;
  --background: #0f172a;
  --text: #f8fafc;
  --text-muted: #cbd5e1;
  --card-bg: rgba(15, 23, 42, 0.8);
  --card-border: rgba(71, 85, 105, 0.3);
  --nav-bg: rgba(15, 23, 42, 0.9);
  --nav-scrolled: rgba(15, 23, 42, 0.98);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--background);
  color: var(--text);
  line-height: 1.6;
  overflow-x: hidden;
}

button, input, textarea {
  font-family: 'Inter', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Layout Styles */
.layout {
  min-height: 100vh;
  width: 100%;
  position: relative;
}

.visualization-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.main-content {
  position: relative;
  z-index: 1;
  min-height: calc(100vh - 60px);
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Home Page Styles */
/* Enhanced Home Page Styles */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 60px);
  text-align: center;
  padding: 0 20px;
  position: relative;
  z-index: 5; /* Ensure content appears above visualization */
}

.name {
  /* Make sure the text is visible with good contrast */
  font-size: 3.5rem;
  color: white !important; /* Using !important to override any conflicting styles */
  background: linear-gradient(to right, var(--primary-light), var(--primary));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0 0 30px rgba(59, 130, 246, 0.5);
}

.title-container {
  margin-bottom: 3rem;
  position: relative;
  z-index: 10;
}

.content-card {
  position: relative;
  z-index: 10;
}

/* Project page styling fixes */
.project-card-wrapper {
  height: 100%;
}

.project-link {
  display: block;
  height: 100%;
  text-decoration: none;
  color: inherit;
}

.project-card {
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.3);
  border-color: var(--primary);
}

.project-image {
  width: 100%;
  height: 180px;
  background-color: rgba(30, 41, 59, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.project-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  font-weight: 600;
  color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.live-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgba(16, 185, 129, 0.9);
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.7rem;
  display: flex;
  align-items: center;
  gap: 4px;
}

.live-badge i {
  color: #10b981;
  font-size: 0.5rem;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

.project-content {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.project-content h3 {
  font-size: 1.3rem;
  margin-bottom: 0.8rem;
  color: var(--primary);
  transition: color 0.3s ease;
}

.project-card:hover .project-content h3 {
  color: var(--primary-light);
}

.project-content p {
  flex-grow: 1;
  margin-bottom: 1rem;
  color: var(--text);
}

.project-link-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
  padding-top: 0.75rem;
  border-top: 1px solid var(--card-border);
  color: var(--primary);
  font-size: 0.9rem;
  font-weight: 500;
  transition: color 0.3s ease;
}

.project-card:hover .project-link-indicator {
  color: var(--primary-light);
}

.project-link-indicator i {
  font-size: 0.8rem;
}

/* Modify existing tech badges */
.tech-badge {
  display: inline-block;
  background-color: rgba(59, 130, 246, 0.15);
  color: var(--primary-light);
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.project-card:hover .tech-badge {
  background-color: rgba(59, 130, 246, 0.2);
}

/* Fix category filters */
.category-filter {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.category-btn {
  background-color: rgba(30, 41, 59, 0.8);
  border: 1px solid var(--card-border);
  color: var(--text);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.category-btn.active {
  background-color: var(--primary);
  border-color: var(--primary);
}

/* Experience page styling fixes */
.experience-item {
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.experience-header h2 {
  margin: 0;
  color: var(--primary);
}

.company-name {
  font-size: 1.1rem;
  margin-bottom: 1rem;
  color: var(--text-muted);
}

.experience-period {
  color: var(--text-muted);
  font-size: 0.9rem;
}

.experience-description {
  padding-left: 1.25rem;
  margin-bottom: 1.5rem;
}

.experience-description li {
  margin-bottom: 0.5rem;
}

.tech-stack h4 {
  margin-bottom: 0.5rem;
  color: var(--text-muted);
}

/* Contact page  */
.contact-title-icon {
  margin-right: 12px;
  color: var(--primary);
}

.intro-icon {
  color: var(--primary);
  margin-right: 8px;
}

.connect-icon {
  margin-right: 10px;
  color: var(--primary);
}

.availability-title-icon {
  margin-right: 8px;
  color: var(--primary);
}

.contact-heading {
  display: flex;
  align-items: center;
}

/* Update for 2-item contact grid instead of 3 */
.contact-methods-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  margin-bottom: 2rem;
}

/* Enhanced method icon styling */
.method-icon {
  font-size: 1.8rem;
  color: white;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary);
  border-radius: 50%;
  box-shadow: 0 4px 10px rgba(59, 130, 246, 0.3);
  transition: transform 0.3s ease;
}

.contact-method-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  background-color: var(--card-bg);
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid var(--card-border);
  transition: all 0.3s ease;
}

.method-content {
  flex: 1;
}

.method-content h3 {
  margin-bottom: 0.5rem;
  color: var(--primary);
}

.method-value {
  color: var(--text);
  text-decoration: none;
  transition: color 0.3s ease;
}

a.method-value:hover {
  color: var(--primary-light);
}

.contact-method-card:hover .method-icon {
  transform: scale(1.1);
}

/* Update for 5 social profiles instead of the previous layout */
.social-profiles-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 1.5rem;
  margin-bottom: 2rem;
}

/* Additional styles to improve the social profile cards */
.social-profile-card {
  padding: 1.5rem 1rem;
  height: 110px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.social-profile-card i {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
}

.social-profile-card:hover i {
  transform: scale(1.2);
}

.social-profile-card span {
  font-weight: 600;
  color: white;
}

/* Enhanced availability card */
.availability-card {
  border-radius: 10px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(59, 130, 246, 0.2);
  background-color: var(--card-bg);
}

.availability-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  color: white;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  box-shadow: 0 6px 12px rgba(59, 130, 246, 0.3);
}

.availability-content {
  flex: 1;
}

.availability-content h3 {
  margin-bottom: 0.5rem;
  color: var(--primary);
  display: flex;
  align-items: center;
}

.availability-content p {
  color: var(--text);
  font-size: 1rem;
  line-height: 1.5;
}

/* Contact intro paragraph with a bit more emphasis */
.contact-intro p {
  font-size: 1.1rem;
  line-height: 1.7;
  text-align: center;
  margin-bottom: 2rem;
}

/* Social section heading enhanced */
.social-section h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .contact-methods-grid {
    grid-template-columns: 1fr;
  }
  
  .social-profiles-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .social-profiles-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Ensure visualization sits behind content */
.visualization-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.clt-visualization {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  pointer-events: none;
}

/* Content Card Styles */
.page-container {
  width: 100%;
  max-width: 1200px;
  padding: 2rem;
  display: flex;
  justify-content: center;
}

.content-card {
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 2.5rem;
  width: 100%;
  max-width: 900px;
  backdrop-filter: blur(10px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.content-card h1 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: var(--primary);
  border-bottom: 1px solid var(--card-border);
  padding-bottom: 1rem;
}

.content-card h2 {
  font-size: 1.6rem;
  margin: 1.5rem 0 1rem;
  color: var(--text);
}

.content-card p {
  margin-bottom: 1rem;
  font-size: 1.05rem;
}

/* About Page Styles */
.about-content {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.5rem;
}

.skill-category h3 {
  color: var(--primary);
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.skill-category ul {
  list-style-type: none;
}

.skill-category li {
  padding: 0.3rem 0;
  color: var(--text);
}

.education-item {
  margin-bottom: 1rem;
}

.education-item h3 {
  color: var(--text);
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
}

.education-item p {
  color: var(--text-muted);
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .name {
    font-size: 2.5rem;
  }
  
  .title {
    font-size: 1.2rem;
  }
  
  .subtitle {
    font-size: 1rem;
  }
  
  .content-card {
    padding: 1.5rem;
  }
  
  .content-card h1 {
    font-size: 2rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 480px) {
  .name {
    font-size: 2rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .content-card {
    padding: 1rem;
  }
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
  }
  
  .contact-info {
    padding-right: 0;
    margin-bottom: 2rem;
  }
}